import React, { Component } from 'react';
import axios from 'axios'
import { Link } from 'react-router-dom'
import { login } from './auth';
import {
  Box,
  Button,
  TextField,
  FormHelperText,
  makeStyles
} from '@material-ui/core';

class SignIn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      errors: ''
     };
     this.handleSubmit = this.handleSubmit.bind(this)
  }
  UNSAFE_componentWillMount() {
    if (this.props.loggedInStatus && this.props.user.admin) {
      return this.props.history.push('/app');
    } else if (this.props.loggedInStatus && !this.props.user.admin) {
      return this.props.history.push('/calendar');
    } else if (!this.props.loggedInStatus) {
      return this.props.history.push('/login');
    } else return null
  }

handleChange = (event) => {
    const {name, value} = event.target
    this.setState({
      [name]: value
    })
  };
handleSubmit(event) {
    event.preventDefault()
    const { email, password} = this.state
let user = {
      email: email,
      password: password
    }
    console.log(user);
    axios.post('http://localhost:3001/login', {user}, {withCredentials: true})
    .then(response => {
      let user = response.data.user
      if (response.data.logged_in) {
        login();
        this.props.handleLogin(response.data)

      } else {
        this.setState({
          errors: response.data.errors
        })
      }
      if (user && user.admin === true) {
        this.props.history.push('/app');
      } else if (user && user.admin === false){
        this.props.history.push('/calendar');
      }
    })
    .catch(error => console.log('api errors:', error))


  };


handleErrors = () => {
    return (
        <Box mt={3}>
          <FormHelperText error>
            {this.state.errors.map(error => {
            return <li key={error}>{error}</li>
              })
            }
          </FormHelperText>
        </Box>
    )
  }
render() {
    const {email, password} = this.state
return (
      <div>
        <form onSubmit={this.handleSubmit}>
          <TextField
            fullWidth
            autoFocus
            label="Email Address"
            margin="normal"
            name="email"
            onChange={this.handleChange}
            type="email"
            value={email}
            variant="outlined"
          />
          <TextField
            fullWidth
            label="Password"
            margin="normal"
            name="password"
            onChange={this.handleChange}
            type="password"
            value={password}
            variant="outlined"
          />
          <Box mt={2}>
            <Button
              color="secondary"
              fullWidth
              size="large"
              type="submit"
              variant="contained"
            >
              Log In
            </Button>

          </Box>
        </form>
        <div>
        {
          this.state.errors ? this.handleErrors() : null
        }
      </div>
    </div>
    );
  }
}
export default SignIn;
